@charset 'utf-8';
/* ------------------------------1. RESET CSS------------------------------*/
html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
cite,
code,
em,
font,
img,
q,
s,
small,
strong,
b,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
section,
nav,
header,
footer,
main,
figure,
figcaption,
fgroup,
time,
hr {
  font-size: 100%;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
  border: 0;
  outline: 0;
  background: transparent;
}
article,
aside,
section,
nav,
header,
footer,
main,
figure,
figcaption,
fgroup,
time,
hr {
  display: block;
}
html {
  -webkit-text-size-adjust: none;
  -ms-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0.1);
}
input::-moz-focus-inner {
  padding: 0;
  border: 0;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
input[type=submit],
input[type=button],
button {
  cursor: pointer;
  -webkit-appearance: none;
}
input,
textarea,
button {
  margin: 0;
  padding: 0;
  border: none;
  border-radius: 0;
  outline: none;
  background: none;
  box-shadow: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  -webkit-focus-ring-color: none;
}
button {
  overflow: visible;
}
body {
  line-height: 1;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
}
:focus {
  outline: 0;
}
:disabled {
  cursor: default;
}
table {
  border-spacing: 0;
  border-collapse: collapse;
}
.clear {
  clear: both;
}
.clearfix:after {
  display: block;
  visibility: hidden;
  clear: both;
  height: 0;
  content: '.';
}
.justify {
  text-align: justify;
}
.justify:after {
  display: inline-block;
  width: 100%;
  height: 0;
  content: '';
  cursor: default;
  overflow: hidden;
}
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
ul li {
  list-style: none;
}
ol {
  list-style: none;
  counter-reset: item;
}
ol li:before {
  content: counter(item, decimal) '. ';
  counter-increment: item;
}
ol ul li:before {
  display: none;
}
img {
  display: block;
}
a {
  -webkit-transition: 0.4s ease-out;
  -ms-transition: 0.4s ease-out;
  transition: 0.4s ease-out;
}
input[type=submit],
button {
  -webkit-transition: 0.4s ease-out;
  -ms-transition: 0.4s ease-out;
  transition: 0.4s ease-out;
}
