/* ------------------------------3. TYPE ELEMENTS------------------------------*/
html {
  height: 100% !important;
  margin: auto;
  position: relative;
}
html.hidden,
html.hidden body {
  height: auto !important;
  overflow: hidden;
}
body {
  min-width: 320px;
  height: 100% !important;
  position: relative;
  font: 300 18px/1.23 'PF Agora Sans Pro', sans-serif;
  background: #f6f7fb;
  color: #2e2e2e;
}
.wrapper {
  max-width: 1240px;
  margin: auto;
  padding: 0 20px;
}
/* links */
a {
  text-decoration: none;
  color: #2e2e2e;
}
.green-link {
  color: #41acad;
  border-bottom: 1px solid #41acad;
}
/* titles, text */
h1 {
  padding: 0 0 17px;
  font: bold 48px/1.1 'TT Prosto Sans';
  color: #2e2e2e;
}
h2 {
  padding: 0 0 17px;
  font: bold 48px/1.1 'TT Prosto Sans';
  color: #2e2e2e;
}
h3 {
  padding: 0 0 17px;
  font: bold 36px/1.15 'TT Prosto Sans';
  color: #2e2e2e;
}
h4 {
  padding: 0 0 17px;
  font: bold 36px/1.15 'TT Prosto Sans';
  color: #2e2e2e;
}
h5 {
  padding: 0 0 17px;
  font: bold 24px/1.2 'TT Prosto Sans';
  color: #2e2e2e;
}
h6 {
  padding: 0 0 17px;
  font: bold 24px/1.2 'TT Prosto Sans';
  color: #2e2e2e;
}
ul,
ol {
  padding: 0 0 21px;
}
p {
  padding: 0 0 21px;
}
img {
  margin: 0 0 24px;
}
table {
  margin: 0 0 21px;
}
/* text inputs */
.input-1 {
  width: 100%;
  height: 53px;
  margin: 0 0 13px;
  padding: 5px 20px 4px;
  background: #fff;
  border: 1px solid #dfdfdf;
  font: italic 300 24px/1 'PF Agora Sans Pro', sans-serif;
  color: #2e2e2e;
}
.input-1.error {
  border-color: #db1e1e;
}
.input-1.error + label.error {
  margin: 0 0 4px;
  display: block;
  position: relative;
  bottom: 6px;
  left: 0;
  font: 16px/1 'PF Agora Sans Pro', sans-serif;
  color: #db1e1e;
}
.input-2 {
  width: 100%;
  height: 40px;
  margin: 0 0 13px;
  padding: 5px 15px 5px;
  background: #fff;
  border: 1px solid #e5e5e5;
  font: 300 18px/1 'PF Agora Sans Pro', sans-serif;
  color: #2e2e2e;
}
.input-2.error {
  border-color: #db1e1e;
}
.input-2.error + label.error {
  margin: 0 0 4px;
  display: block;
  position: relative;
  bottom: 6px;
  left: 0;
  font: 16px/1 'PF Agora Sans Pro', sans-serif;
  color: #db1e1e;
}
.textarea-1 {
  min-width: 100%;
  max-width: 100%;
  height: 148px;
  min-height: 148px;
  margin: 0 0 13px;
  padding: 8px 15px 7px;
  background: #fff;
  border: 1px solid #e5e5e5;
  font: 300 18px/1.2 'PF Agora Sans Pro', sans-serif;
  color: #2e2e2e;
  resize: none;
}
/* placeholder */
*::-moz-placeholder {
  opacity: 1 !important;
  color: #d6d6d6 !important;
}
*::-webkit-input-placeholder {
  line-height: normal;
  opacity: 1 !important;
  color: #d6d6d6 !important;
}
*:-ms-input-placeholder {
  opacity: 1 !important;
  color: #d6d6d6 !important;
}
/* buttons */
.button {
  min-width: 188px;
  height: 47px;
  padding: 0 20px;
  display: inline-block;
  font: 300 24px/47px 'PF Agora Sans Pro', sans-serif;
  background: #41acad;
  color: #fff;
  text-align: center;
  white-space: nowrap;
}
.action-button {
  padding: 0 0 0 74px;
  display: inline-block;
  position: relative;
  -webkit-transition: none ease-out;
  -ms-transition: none ease-out;
  transition: none ease-out;
}
.action-button span {
  display: block;
  position: relative;
  top: 1px;
  color: #2e2e2e;
  border-bottom: 1px solid #2e2e2e;
  font: 300 18px/0.9 'PF Agora Sans Pro', sans-serif;
  -webkit-transition: 0.4s ease-out;
  -ms-transition: 0.4s ease-out;
  transition: 0.4s ease-out;
}
.action-button:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  background: url(../img/sprite.png) no-repeat;
  background-size: 500px 500px;
}
.action-button_icon-1:before {
  width: 54px;
  height: 54px;
  margin: -30px 0 0;
  background-position: -59px 0;
}
.action-button_icon-2:before {
  width: 57px;
  height: 49px;
  margin: -24.5px 0 0;
  background-position: -119px 0;
}
.action-button_icon-3:before {
  width: 51px;
  height: 51px;
  margin: -25.5px 0 0;
  background-position: -180px 0;
}
.action-button_icon-4:before {
  width: 55px;
  height: 56px;
  margin: -28px 0 0 -12px;
  background-position: -59px -134px;
}
.white-button {
  min-width: 188px;
  height: 52px;
  padding: 0 20px 2px;
  display: inline-block;
  font: 300 24px/48px 'PF Agora Sans Pro', sans-serif;
  color: rgba(54, 54, 54, 0.48);
  text-align: center;
  border: 1px solid #d9d9d9;
}
a.white-button {
  padding-bottom: 0;
}
.border-button {
  min-width: 268px;
  height: 45px;
  padding: 0 13px 2px;
  display: inline-block;
  font: 300 18px/43px 'PF Agora Sans Pro', sans-serif;
  color: #41acad;
  text-align: center;
  border: 1px solid #41acad;
}
a.border-button {
  padding-bottom: 0;
}
.response-button {
  min-width: 544px;
  height: 85px;
  margin: 0 0 20px;
  padding: 0 30px;
  display: inline-block;
  background: #41acad;
  font: bold 36px/84px 'PF Agora Sans Pro', sans-serif;
  color: #fff;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
}
.response-button span {
  padding: 0 0 0 84px;
  position: relative;
  left: 8px;
}
.response-button span:before {
  content: "";
  width: 57px;
  height: 56px;
  margin: -28px 0 0;
  position: absolute;
  top: 50%;
  left: 0;
  background: url(../img/sprite.png) no-repeat -59px -206px;
  background-size: 500px 500px;
}
.large-button {
  min-width: 544px;
  height: 85px;
  margin: 0 0 20px;
  padding: 0 30px;
  display: inline-block;
  font: bold 36px/84px 'PF Agora Sans Pro', sans-serif;
  background: #41acad;
  color: #fff;
  text-align: center;
  white-space: nowrap;
}
/* load icon */
.loaded {
  overflow: hidden;
}
.loaded * {
  -webkit-transition: none ease-out;
  -ms-transition: none ease-out;
  transition: none ease-out;
}
.loaded .main-wrap {
  visibility: hidden;
}
.loaded .icon-load {
  display: block;
}
.icon-load {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  z-index: 999999999;
  position: fixed;
  top: 0;
  left: 0;
  background: #f6f7fb;
  display: none;
}
.icon-load:before {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 170px;
  height: 170px;
  margin-top: -85px;
  margin-left: -85px;
  content: '';
  background: url(../img/loader.gif) no-repeat left top;
}
/* ------------------------------4. HEADER------------------------------*/
.header {
  background: #fff;
}
.header__cols {
  width: 100%;
  display: table;
}
.header__left {
  padding: 26px 10px 28px 10px;
  display: table-cell;
  vertical-align: top;
}
.header__right {
  padding: 15px 0;
  display: table-cell;
  vertical-align: middle;
  text-align: right;
}
.header__mask {
  display: none;
}
.logo-1 {
  display: inline-block;
  white-space: nowrap;
  text-align: left;
}
.logo-1__title {
  margin: 0 10px 0 0;
  padding: 0 17px 0 0;
  display: inline-block;
  vertical-align: middle;
  position: relative;
  font: 900 37px/1 'TT Prosto Sans';
  color: #41acad;
  letter-spacing: -0.65px;
}
.logo-1__title:after {
  content: "";
  height: 42px;
  margin: -23px 0 0;
  position: absolute;
  top: 50%;
  right: 0;
  border-left: 2px solid #cbcbcb;
}
.logo-1__text {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  bottom: 2px;
  font: 300 18px/0.95 'TT Prosto Sans';
  color: #303131;
}
.login {
  padding: 0 0 0 39px;
  display: inline-block;
  position: relative;
  top: 2px;
  text-align: left;
}
.login:before {
  content: "";
  width: 24px;
  height: 28px;
  margin: -16px 0 0;
  position: absolute;
  top: 50%;
  left: 0;
  background: url(../img/sprite.png) no-repeat;
}
.login__list {
  padding: 0;
}
.login__item {
  max-width: 100%;
  margin: 0 4px 0 0;
  padding: 0 10px 0 0;
  display: inline-block;
  vertical-align: top;
  position: relative;
}
.login__item:before {
  content: "";
  height: 16px;
  margin: -8px 0 0;
  position: absolute;
  top: 50%;
  right: 0;
  border-left: 2px solid #878787;
}
.login__item:last-child {
  margin: 0;
  padding: 0;
}
.login__item:last-child:before {
  display: none;
}
.login__link {
  display: inline-block;
  line-height: 1.2;
  color: #4e4e4e;
  word-break: break-all;
}
.header-nav {
  height: 54px;
  background: #163d54;
}
.header-nav__list {
  max-width: 1128px;
  height: 100%;
  padding: 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: space-between;
  -ms-justify-content: space-between;
  justify-content: space-between;
}
.header-nav__item {
  height: 100%;
  margin: 0 -59px;
  padding: 0 59px;
  position: relative;
}
.header-nav__item_more {
  margin: 0 -64px;
  padding: 0 64px;
}
.header-nav__item_more .header-nav__link {
  margin: 0 -64px;
  padding: 0 64px;
}
.header-nav__link {
  height: 100%;
  margin: 0 -59px;
  padding: 0 59px;
  display: block;
  color: #fff;
  line-height: 54px;
  text-align: center;
  white-space: nowrap;
  -webkit-transition: none ease-out;
  -ms-transition: none ease-out;
  transition: none ease-out;
}
.header-nav__link.active {
  background: #6daaab;
}
.header-nav__close {
  display: none;
}
.hide-nav {
  min-width: 100%;
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 10;
  display: none;
}
.hide-nav__list {
  margin: 1px 0 0;
  padding: 11px 0 21px;
  background: #163d54;
}
.hide-nav__link {
  padding: 5.5px 19px 5.5px;
  display: block;
  line-height: 1.4;
  color: #fff;
  -webkit-transition: none ease-out;
  -ms-transition: none ease-out;
  transition: none ease-out;
  white-space: nowrap;
}
.mob-button {
  display: none;
}
/* ------------------------------5. FOOTER------------------------------*/
.footer {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
  background: #fff;
  color: #a2a2a2;
}
.footer__main {
  padding-top: 23px;
  padding-bottom: 20px;
}
.footer__cols {
  padding: 0 0 19px;
  width: 100%;
  display: table;
}
.footer__left {
  padding: 0 20px 0 0;
  display: table-cell;
  vertical-align: middle;
}
.footer__right {
  padding: 0 8px 0 0;
  display: table-cell;
  vertical-align: middle;
  text-align: right;
}
.footer-nav__list {
  padding: 0 0 33px;
  display: -webkit-box;
  display: -webkit-flex;
