/*!---------------8.3 Star Rating---------------*/
.rating {
  height: 64px;
  display: inline-block;
  vertical-align: top;
  white-space: nowrap;
  text-align: center !important;
}
.star-rating {
  width: 34px !important;
  cursor: pointer;
  float: left;
  position: relative;
  background: transparent !important;
  overflow: hidden !important;
}
.star-rating:nth-child(2n+1) {
  margin: 0 14px 0 0;
}
.star-rating:last-child {
  margin: 0;
}
.star-rating:nth-child(2n-1) a:before,
.star-rating:last-child a:before {
  left: auto;
  right: 0;
}
.star-rating a {
  width: 34px !important;
  height: 64px;
  display: block;
  text-indent: -9999px;
}
.star-rating a:before {
  content: "";
  width: 68px;
  height: 64px;
  position: absolute;
  top: 0;
  left: 0;
  background: url(../img/sprite.png) no-repeat -329px -417px;
}
.star-rating-on a:before {
  background-position: -253px -417px !important;
}
.star-rating-hover a:before {
  background-position: -253px -417px;
}
.star-rating-readonly a {
  cursor: default;
}
.rating-cancel {
  display: none !important;
}
.rating_small {
  height: 38px;
}
.rating_small .star-rating {
  width: 20px !important;
}
.rating_small .star-rating:nth-child(2n+1) {
  margin: 0 6px 0 0;
}
.rating_small .star-rating:last-child {
  margin: 0;
}
.rating_small .star-rating a {
  width: 20px !important;
  height: 38px;
}
.rating_small .star-rating a:before {
  width: 40px;
  height: 38px;
