/* ------------------------------3. TYPE ELEMENTS------------------------------*/
html{height:100% !important; margin:auto; position:relative;}
html.hidden, html.hidden body{height:auto !important; overflow:hidden;}
body{min-width:320px; height:100% !important; position:relative; font:@base-font-weight @base-font-size/@base-line-height @font-1; background:@base-bg; color:@text-color;}
.wrapper{max-width:1240px; margin:auto; padding:0 20px;}

/* links */
a{text-decoration:none; color:@link-color;}
.green-link{color:@green; border-bottom:1px solid @green;}

/* titles, text */
h1{padding:0 0 17px; .title-1; color:@title-color;}
h2{padding:0 0 17px; .title-2; color:@title-color;}
h3{padding:0 0 17px; .title-3; color:@title-color;}
h4{padding:0 0 17px; .title-4; color:@title-color;}
h5{padding:0 0 17px; .title-5; color:@title-color;}
h6{padding:0 0 17px; .title-6; color:@title-color;}
ul, ol{padding:0 0 21px;}
p{padding:0 0 21px;}
img{margin:0 0 24px;}
table{margin:0 0 21px;}

/* text inputs */
.input-1{width:100%; height:53px; margin:0 0 13px; padding:5px 20px 4px; background:#fff; border:1px solid #dfdfdf; font:italic 300 24px/1 @font-1; color:@black;}
.input-1.error{border-color:#db1e1e;}
.input-1.error + label.error{margin:0 0 4px; display:block; position:relative; bottom:6px; left:0; font:16px/1 @font-1; color:#db1e1e;}
.input-2{width:100%; height:40px; margin:0 0 13px; padding:5px 15px 5px; background:#fff; border:1px solid #e5e5e5; font:300 18px/1 @font-1; color:@black;}
.input-2.error{border-color:#db1e1e;}
.input-2.error + label.error{margin:0 0 4px; display:block; position:relative; bottom:6px; left:0; font:16px/1 @font-1; color:#db1e1e;}

.textarea-1{min-width:100%; max-width:100%; height:148px; min-height:148px; margin:0 0 13px; padding:8px 15px 7px; background:#fff; border:1px solid #e5e5e5; font:300 18px/1.2 @font-1; color:@black; resize:none;}

/* placeholder */
*::-moz-placeholder{opacity:1 !important; color:#d6d6d6 !important;}
*::-webkit-input-placeholder{line-height:normal; opacity:1 !important; color:#d6d6d6 !important;}
*:-ms-input-placeholder{opacity:1 !important; color:#d6d6d6 !important;}

/* buttons */
.button{min-width:188px; height:47px; padding:0 20px; display:inline-block; font:300 24px/47px @font-1; background:@green; color:#fff; text-align:center; white-space:nowrap;}

.action-button{padding:0 0 0 74px; display:inline-block; position:relative; .transition(none);}
.action-button span{display:block; position:relative; top:1px; color:@black; border-bottom:1px solid @black; font:300 18px/0.9 @font-1; .transition;}
.action-button:before{content:""; position:absolute; top:50%; left:0; background:url(../img/sprite.png) no-repeat; background-size:500px 500px;}
.action-button_icon-1:before{width:54px; height:54px; margin:-30px 0 0; background-position:-59px 0;}
.action-button_icon-2:before{width:57px; height:49px; margin:-24.5px 0 0; background-position:-119px 0;}
.action-button_icon-3:before{width:51px; height:51px; margin:-25.5px 0 0; background-position:-180px 0;}
.action-button_icon-4:before{width:55px; height:56px; margin:-28px 0 0 -12px; background-position:-59px -134px;}

.white-button{min-width:188px; height:52px; padding:0 20px 2px; display:inline-block; font:300 24px/48px @font-1; color:rgba(54,54,54,0.48); text-align:center; border:1px solid #d9d9d9;}
a.white-button{padding-bottom:0;}

.border-button{min-width:268px; height:45px; padding:0 13px 2px; display:inline-block; font:300 18px/43px @font-1; color:@green; text-align:center; border:1px solid @green;}
a.border-button{padding-bottom:0;}

.response-button{min-width:544px; height:85px; margin:0 0 20px; padding:0 30px; display:inline-block; background:@green; font:bold 36px/84px @font-1; color:#fff; text-align:center; white-space:nowrap; overflow:hidden;}
.response-button span{padding:0 0 0 84px; position:relative; left:8px;}
.response-button span:before{content:""; width:57px; height:56px; margin:-28px 0 0; position:absolute; top:50%; left:0; background:url(../img/sprite.png) no-repeat -59px -206px; background-size:500px 500px;}

.large-button{min-width:544px; height:85px; margin:0 0 20px; padding:0 30px; display:inline-block; font:bold 36px/84px @font-1; background:@green; color:#fff; text-align:center; white-space:nowrap;}

/* load icon */
.loaded{overflow:hidden;}
.loaded *{.transition(none);}
.loaded .main-wrap{visibility:hidden;}
.loaded .icon-load{display:block;}
.icon-load{width:100%;height:100%; margin:0; padding:0; z-index:999999999;position:fixed;top:0;left:0;background:@base-bg;display:none;}
.icon-load:before{position:fixed;top:50%;left:50%;width:170px;height:170px;margin-top:-85px;margin-left:-85px;content:'';background:url(../img/loader.gif) no-repeat left top;}

/* ------------------------------4. HEADER------------------------------*/
.header{background:#fff;}

.header__cols{.table;}
.header__left{padding:26px 10px 28px 10px; .table-cell;}
.header__right{padding:15px 0; .table-cell(middle); text-align:right;}
.header__mask{display:none;}

.logo-1{display:inline-block; white-space:nowrap; text-align:left;}
.logo-1__title{margin:0 10px 0 0; padding:0 17px 0 0; display:inline-block; vertical-align:middle; position:relative; font:900 37px/1 @font-2; color:@green; letter-spacing:-0.65px;}
.logo-1__title:after{content:""; height:42px; margin:-23px 0 0; position:absolute; top:50%; right:0; border-left:2px solid #cbcbcb;}
.logo-1__text{display:inline-block; vertical-align:middle; position:relative; bottom:2px; font:300 18px/0.95 @font-2; color:#303131;}

.login{padding:0 0 0 39px; display:inline-block; position:relative; top:2px; text-align:left;}
.login:before{content:""; width:24px; height:28px; margin:-16px 0 0; position:absolute; top:50%; left:0; background:url(../img/sprite.png) no-repeat;}
.login__list{padding:0;}
.login__item{max-width:100%; margin:0 4px 0 0; padding:0 10px 0 0; display:inline-block; vertical-align:top; position:relative;}
.login__item:before{content:""; height:16px; margin:-8px 0 0; position:absolute; top:50%; right:0; border-left:2px solid #878787;}
.login__item:last-child{margin:0; padding:0;}
.login__item:last-child:before{display:none;}
.login__link{display:inline-block; line-height:1.2; color:#4e4e4e; word-break:break-all;}

.header-nav{height:54px; background:@blue;}
.header-nav__list{max-width:1128px; height:100%; padding:0; .flex; .flex-justify;}
.header-nav__item{height:100%; margin:0 -59px; padding:0 59px; position:relative;}
.header-nav__item_more{margin:0 -64px; padding:0 64px;}
.header-nav__item_more .header-nav__link{margin:0 -64px; padding:0 64px;}
.header-nav__link{height:100%; margin:0 -59px; padding:0 59px; display:block; color:#fff; line-height:54px; text-align:center; white-space:nowrap; .transition(none);}
.header-nav__link.active{background:#6daaab;}
.header-nav__close{display:none;}

.hide-nav{min-width:100%; position:absolute; top:100%; left:0; z-index:10; display:none;}
.hide-nav__list{margin:1px 0 0; padding:11px 0 21px; background:@blue;}
.hide-nav__link{padding:5.5px 19px 5.5px; display:block; line-height:1.4; color:#fff; .transition(none); white-space:nowrap;}

.mob-button{display:none;}

/* ------------------------------5. FOOTER------------------------------*/
.footer{width:100%; position:absolute; bottom:0; left:0; z-index:1; background:#fff; color:#a2a2a2;}
.footer__main{padding-top:23px; padding-bottom:20px;}
.footer__cols{padding:0 0 19px; .table;}
.footer__left{padding:0 20px 0 0; .table-cell(middle);}
.footer__right{padding:0 8px 0 0; .table-cell(middle); text-align:right;}

.footer-nav__list{padding:0 0 33px; .flex; .flex-justify;}
.footer-nav__link{padding:0 4px; display:block; line-height:2; color:#a2a2a2; white-space:nowrap;}

.logo-2{display:inline-block; white-space:nowrap; text-align:left;}
.logo-2__title{margin:0 10px 0 0; padding:0 15px 0 0; display:inline-block; vertical-align:middle; position:relative; font:900 37px/1 @font-2; color:@green; letter-spacing:-0.65px;}
.logo-2__title:after{content:""; height:42px; margin:-23px 0 0; position:absolute; top:50%; right:0; border-left:2px solid #cbcbcb;}
.logo-2__text{display:inline-block; vertical-align:middle; position:relative; bottom:2px; font:300 18px/0.95 @font-2; color:#303131;}

.copyrights{padding:0; position:relative; top:2px;}

/* ------------------------------6. MAIN------------------------------*/
.main-wrap{min-height:100%; margin:auto; position:relative; overflow:hidden;}

/*!---------------6.1 MAIN PAGE---------------*/
.main-block{padding-top:70px;}
.main-block__top{padding:0 0 34px;}
.main-block__title-1{padding:0; .title-3; color:@blue; text-transform:uppercase;}
.main-block__title-2{padding:0 0 10px; font:italic 300 18px/1.2 @font-2; color:@blue;}

.main-nav{margin:0 -71px 0 0; padding:0 0 18px; overflow:hidden;}
.main-nav__item{margin:0 71px 5px 0; display:inline-block; vertical-align:top;}
.main-nav__link{display:inline-block; white-space:nowrap; font:300 24px/47px @font-1;color: #000; .transition(none);}
.main-nav__link span{border-bottom:1px dotted @link-color; .transition;}

.rankings{padding:0 0 52px; text-align:center;}
.rankings__list{padding:0 0 25px; text-align:left;}
.rankings__button{width:100%; min-width:0 !important; max-width:390px; margin:0 0 10px; padding:0;}
.rankings__item{min-height:70px; margin:0 0 9px; position:relative; background:#fff; box-shadow:0 0 4px rgba(54,54,54,0.15); .transition;}
.rankings__item:before{display:none;}
.rankings__item.active .ranking-content__button{background:@green;}
.rankings__item.active .ranking-content__button:before{margin-top:-8px; .rotate(180deg);}

.ranking-content{padding:28px 40px 26px; .flex; position:relative;}
.ranking-content__button{width:40px; height:35px; position:absolute; top:16px; right:0; z-index:1; background:#c0c0c0;}
.ranking-content__button:before{content:""; width:21px; height:14px; margin:-5.5px 0 0 -10.5px; position:absolute; top:50%; left:50%; background:url(../img/sprite.png) no-repeat -27px 0; background-size:500px 500px; .transition(0.3s);}
.ranking-content__col:nth-child(1){.flex-basis(6.88%);}
.ranking-content__col:nth-child(2){.flex-basis(16.9%); padding:0 30px 0 0;}
.ranking-content__col:nth-child(3){.flex-basis(26.96%); padding:0 30px 0 0;}
.ranking-content__col:nth-child(4){.flex-basis(20.89%); padding:0 25px 0 0;}
.ranking-content__col:nth-child(5){.flex-basis(11.66%); text-align:center;}
.ranking-content__col:nth-child(6){.flex-basis(16.71%); padding:0 8px 0 0; text-align:center;}
.ranking-content__numbers{min-width:50px; display:inline-block; position:relative; right:11px; text-align:center;}
.ranking-content__position{padding:0 0 2px; display:block; position:relative; font:bold 48px/0.9 @font-1;}
.ranking-content__change{display:block; position:relative; right:1px; font:300 24px/1 @font-1;}
.ranking-content__change_green{color:@green;}
.ranking-content__change_grey{color:#b8b8b8;}
.ranking-content__change_red{color:#e18d99;}
.ranking-content__image{margin:4px 0 0; display:block;}
.ranking-content__image img{max-width:100%; margin:0;}
.ranking-content__title{padding:1px 0 2px; font:bold 24px/1.2 @font-1;}
.ranking-content__url{color:@green; border-bottom:1px solid @green;}
.ranking-content__desc{padding:3px 0 0;}
.ranking-content__label{padding:5px 0 5px; font:300 14px/1.2 @font-1; color:rgba(46,46,46,0.45);}
.ranking-content__rating{padding:0;}
.ranking-content__rating span{font-size:36px; color:@green;}
.ranking-content__price{padding:6px 0 0; font-size:30px; color:@green;}

.ranking-hide{display:none;}
.ranking-hide__cols{padding:31px 40px 2px; .table; border-top:1px solid #e1e1e1;}
.ranking-hide__left{padding:0 40px 0 0; .table-cell;}
.ranking-hide__right{width:305px; .table-cell;}
.ranking-hide__content{max-width:600px;}

.more-nav{margin:0 -41px 0 0; padding:0 4px 15px;}
.more-nav__item{padding:0 41px 10px 0; display:inline-block; vertical-align:top;}
.more-nav__item:before{display:none;}
.more-nav__link{display:inline-block; color:@green; border-bottom:1px solid @green; line-height:0.9;}

.ranking-total{padding:0 0 23px;}
.ranking-total p{padding:0 0 1px;}
.ranking-total p span{font-size:24px;}

.socials-1__title{padding:0 0 7px; font-weight:bold;}
.socials-1__list{margin:0 -3px 0 0; padding:0 0 10px;}
.socials-1__item{padding:0 3px 8px 0; display:inline-block; vertical-align:top;}
.socials-1__item:before{display:none;}
.socials-1__link{width:49px; height:49px; display:block; background:url(../img/sprite.png); background-size:500px 500px;}
.socials-1__link_icon-1{background-position:0 -19px;}
.socials-1__link_icon-2{background-position:0 -73px;}
.socials-1__link_icon-3{background-position:0 -127px;}
.socials-1__link_icon-4{background-position:0 -181px;}

.actions{.table; border-top:1px solid #e1e1e1;}
.actions__link{.w33; height:97px; .table-cell(middle); text-align:center; border-right:1px solid #e1e1e1;}
.actions__link:last-child{border:none;}
.actions__link .action-button_icon-1{left:18px;}
.actions__link .action-button_icon-3{left:3px;}

