.form__label-flex{
	display: flex;
	align-items: center;
	span{
		font-size: 14px;
		padding-left: 5px;
	}
}
.form__item_full{
	padding-bottom: 37px;
}
.form-complaint{
	.form__title{
		padding-bottom: 36px;
	}
	.form__item{
		padding-bottom: 10px;
	}
	.select .jq-selectbox__select-text{
		padding-left: 10px;
	}
}
.form-notification{
	.form__title{
		padding-bottom: 19px;
	}
	.form__text{
		padding-bottom: 11px;
	}
}









@media screen and (max-width:1200px){
	.articles-wrap{
		padding: 30px 0;
	}
	.text-center{
		padding-right: 0;
	}
	.main-block-articles .articles-wrap{
		padding: 43px 40px 40px 40px;
	}
	.about-content{
		padding: 40px 0 40px 40px;
	}
	.articles-item-cont_full{
		margin-bottom: 0;
	}
	.add-reviews{
		padding: 40px 0 30px 20px;
	}
	.add-reviews .articles-item-cont{
		padding-right: 20px;
	}
	.aside-banner{
		width: 400px;
		height: 450px;
	}
	.main-nav-inner{
		padding: 0;
		justify-content: space-between;
	}
	.main-nav-inner .main-nav__item{
		margin-right: 0;
	}
	.aside-banner__title{
		font-size: 40px;
		line-height: 45px;
	}
}
@media screen and (max-width:1023px){
	.articles-wrap{
		padding: 30px 0;
	}
	.articles-list__item{
		width: 50%;
	}
	.main-block-articles .articles-wrap{
		padding: 20px;
	}
	.main-block-articles h1{
		max-width: 100%;
		font-size: 32px;
		line-height: 40px;
	}
	.articles-item-row{
		flex-direction: column;
	}
	.articles-item-cont{
		max-width: 100%;
		padding-right: 0;
		width: 100%;
	}
	.articles-item-cont p{
		max-width: 100%;
	}
	.rating-nav__link{
		font-size: 18px;
		line-height: 22px;
		padding: 10px 11px 0;
		height: 40px;
	}
	.about-content{
		padding: 30px 20px;
		.articles-item-aside{
			display: none;
		}
	}
	.add-reviews .articles-item-aside{
		display: none;
	}
	.aside-banner{
		display: none;
	}
	.articles-item-cont .step-list li{
		max-width: 100%;
	}
	.add-reviews{
		padding: 40px 20px 30px 20px;
	}
	.add-reviews .articles-item-cont{
		padding-right: 00px;
	}

}
@media screen and (max-width:767px){
	.articles-list__item{
		padding: 0 8px;
	}
	.articles-list__title{
		font-size: 22px;
		line-height: 24px;
	}
	.articles-list__item p{
		font-size: 16px;
		line-height: 22px;
	}
	.articles-list__more{
		font-size: 16px;
	}
	.articles-back{
		font-size: 20px;
		line-height: 24px;
	}
	.main-block-articles h1{
		font-size: 28px;
		line-height: 32px;
		margin-bottom: 5px;
	}
	.articles-item-cont{
		h2{
			line-height: 20px;
			font-size: 26px;
			padding: 25px 0 4px 0;

		}
		h3{
			font-size: 18px;
			line-height: 24px;
			padding: 30px 0 8px 0;
		}
		p{
			line-height: 22px;
			font-size: 16px;
		}
	}
	.articles-item-cont {
		li{
			padding-left: 22px;
			font-size: 16px;
			line-height: 22px;
			margin-bottom: 14px;
			max-width: 100%;
			&:before{
				top: 9px;
				width: 7px;
				height: 7px;
			}
		}
	}
	.inner-rating{
		padding-top: 30px;
	}
	.list-two-col{
		column-count: 1;
		padding-bottom: 0;
	}
	.rating-nav{
		flex-wrap: wrap;
		justify-content: center;
		padding-bottom: 0;
	}
	.rating-nav__item{
		margin-bottom: 10px;
		margin-right: 15px;
	}
	.step-list__number{
		font-size: 60px;
		line-height: 45px;
		top: 0;
	}
	.articles-item-cont .step-list li{
		padding-left: 45px;
	}
	.participant-content h2{
		margin-bottom: 15px;
	}
	.participant-content h2.step-list-title{
		padding-top: 30px;
	}
	.form__label-flex{
		flex-wrap: wrap;
	}
	.form__label-flex span{
		display: block;
		width: 100%;
		padding: 0 0 10px 0;
	}
	.form__item_full{
		padding-bottom: 17px;
	}
	.add-reviews{
		padding: 30px 15px;
	}
	.main-nav-inner{
		flex-wrap: wrap;
		margin-left: -7px;
		margin-right: -7px;
		justify-content: flex-start;
	}
	.main-nav-inner .main-nav__item{
		padding: 0 7px;
	}
}
@media screen and (max-width:480px){
	.articles-list__item{
		width: 100%;
		padding: 0 15px;
		margin-bottom: 40px;
	}
	.text-center{
		padding: 0 15px;
	}
	.articles-list__title{
		font-size: 20px;
		line-height: 22px;
	}
	.articles-list__item p{
		font-size: 14px;
		line-height: 20px;
	}
	.articles-list__more{
		font-size: 14px;
	}
	.articles-list__img img{
		margin-bottom: 10px;
	}
	.main-block-articles .articles-wrap{
		padding: 15px;
	}
	.main-block-articles h1{
		font-size: 24px;
		line-height: 28px;
	}
	.articles-item-cont{
		h2{
			line-height: 26px;
			font-size: 22px;
			padding: 15px 0 4px 0;

		}
		h3{
			font-size: 16px;
			line-height: 22px;
			padding: 25px 0 8px 0;
		}
		p{
			line-height: 20px;
			font-size: 14px;
		}

	}
	.articles-item-cont {
		li{
			padding-left: 10px;
			font-size: 14px;
			line-height: 22px;
			margin-bottom: 14px;
			max-width: 100%;
			&:before{
				top: 7px;
				width: 5px;
				height: 5px;
			}
		}
	}
	.articles-list-vertical .articles-list__item{
		margin-bottom: 30px;
		&:last-child{
			margin-bottom: 0;
		}
	}
	.rating-nav__link{
		font-size: 16px;
		line-height: 20px;
		height: 37px;
		padding: 9px 15px 0;
	}
	.step-list__number{
		font-size: 40px;
		line-height: 35px;
		top: 0;
	}
	.articles-item-cont .step-list li{
		padding-left: 30px;
		margin-bottom: 15px;
		&:last-child{
			margin-bottom: 0;
		}
	}
	.participant-content h2{
		margin-bottom: 10px;
	}
	.participant-content h2.step-list-title{
		padding-top: 15px;
	}
	.articles-item-cont .step-list{
		margin: 0 0 15px 0;
	}
	.articles-item-cont .step-list p{
		padding-bottom: 10px;
	}
	.form-complaint .form__title{
		padding-bottom: 18px;
	}

	.main-nav-inner .main-nav__item{
		padding: 0 4px;
	}
	.main-nav-inner .main-nav__item:nth-child(1),
	.main-nav-inner .main-nav__item:nth-child(2),
	.main-nav-inner .main-nav__item:nth-child(3){
		padding: 0 8px;
	}
}
@media screen and (max-width: 353px){	
	.main-nav__button{
		padding: 0 8px;
		min-width: 110px;
		font: 300 14px/40px 'PF Agora Sans Pro', sans-serif;
	}
}
/*new(23.11) inner page end*/


.select:hover{
	background-color: red;
}
#-error{
	white-space: nowrap;
	left: 0;
}
#service-error{
	position: absolute;
	bottom: -22px;
	z-index: 99;
	font: 16px/1 'PF Agora Sans Pro', sans-serif;
    color: #db1e1e;
}
.jq-selectbox label.error + .jq-selectbox__select .jq-selectbox__select-text{
    border-color: #fa3352;
    color: #fa3352;
}
.error{
		font: 16px/1 'PF Agora Sans Pro', sans-serif;
    color: #db1e1e;
}
textarea.error {
    border-color: #db1e1e;
}
.textarea-1.error{
	margin-bottom: 0;
}
.error + .add-photo__button .add-photo__fix-2{
	border-color: #db1e1e;
}
#photo-error{
	position: absolute;
	z-index: 99;
	bottom: -22px;
}
@import "_reviews.less";